import React from 'react';
import '../styles/style.css';
import { NavLink } from 'react-router-dom';
import { addStage, addNewBoard, changeView } from '../actions/actions'; /** addComment, nextTitle, handleLoginClick, handleLogoutClick */
import Head from './Head'
import Footer from './footer'
import { connect } from 'redux-zero/react';
import StageView from './stage';
import { board } from '../store/dataBase';

const OtherBoard = ({ tboard }) => {
    /*const OtherBoardComponent = tboard.map((item, index) => {
        return <DetailedBoard
            key={index}
            name={item.name}
            index={index}
        />
    })*/

    return (
        <section>
            <header className="view-header">
                <h3><i className="fa fa-users"></i><span> Other boards</span></h3>
            </header>
            <div className='boards-wrapper'>
                {/*OtherBoardComponent*/}
                {
                    tboard.map((list, index) => {
                        return (
                            <div className='board' key={index}>
                                <div className='inner'>
                                    <h4>{list.name}</h4>
                                </div>
                            </div>
                        );
                    })
                }
            </div>
        </section>
    )
}

class Board extends React.Component {
    render() {
        const { title, boardId, stages, boards } = this.props;
        let list = null;
        if (stages)
            list = stages.map(stage => {
                return <StageView key={stage.id} boards={boardId} title={stage.title} stageId={stage.id} />
            });
        return (
            <div className='board'>
                <div className='inner'>
                    <h4><NavLink onClick={() => { changeView(boardId) }} to="/details/">{title}</NavLink></h4>
                </div>
            </div>
        )
    }
}

class NewBoard extends React.Component {
    render() {
        const { user } = this.props;
        return (
            <div>
                <div className='board form'>
                    <div className='inner'>
                        <h4>New board</h4>
                        <form onSubmit={(e) => {
                            e.preventDefault();
                            addNewBoard(this.boardInputRef.value, user.id);
                        }} id='new_board_form'>
                            <div className="inner-wrap">
                                <input type="text" id="board_name" name="name" placeholder="User"
                                    ref={e => this.boardInputRef = e}
                                />
                                <button type="submit" name="submit">Create board</button>
                                <span> or </span>
                                <a>Cancel</a>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}

const BoardView = ({ user, boards, tboard, stages }) => {
    let list = null;
    if (boards)
        list = boards.map(board => {
            //const path = "/board/" + board.name;
            return (
                <Board key={board.user_id} title={board.title} boardId={board.user_id} stages={stages} boards={board}/>
            )
        });

    return (
        <main id='main_container'>
            <div>
                <div id='authentication_container' className='application-container'>
                    <Head />
                    <div className='main-container'>
                        <div className="view-container boards index">
                            <section>
                                <header className="view-header" >
                                    <h3><i className="fa fa-user"></i><span> My boards</span></h3>
                                </header>
                                <div className="boards-wrapper">
                                    {list}
                                    <NewBoard user={user} />
                                </div>
                            </section>
                            <OtherBoard tboard={tboard} />
                        </div>
                    </div>
                </div>
            </div>
            {/*<Footer />*/}
        </main>
    );
}


const mapToProps = ({ user, boards, tboard, stages }) => ({ user, boards, tboard, stages });
export default connect(mapToProps)(BoardView);


