import createStore from 'redux-zero';
import { otherBoard } from './dataBase';

const initialState = {
    tboard: otherBoard,
    boardId: null,
    successLogin: false,
    user: {
        id: null,
        email: null,
        firstname: null,
        lastname: null
    },
    boards: null,
    stages: null,
    tasks: null
};

const store = createStore(initialState);
export default store;


